.logo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto;
    margin: 0 auto;
    width: 100%;
    place-items: center;
    grid-gap: 1rem;
    padding: 1rem .5rem 0 .5rem;
    background-color: rgba(232, 231, 248, 0.3);
    color: black;
}

.logo-container > img {
    width: 2.75rem;
}

.logo-container > h1 {
    padding-bottom: 1rem;
    font-weight: 300;
}

.technologies {
    width: 100%;
    margin: 0 auto;
    padding-top: 3.9rem;
}

.technologies > h1 {
    font-size: 2rem;
    margin: 0;
    width: 100%;
    padding: 1rem;
    background-color: rgba(145, 96, 230, 0.5);
    color: white;
}

.technologies > p {
    font-size: 1.5rem;
    margin: 1rem 1rem;
    font-weight: bold;
    color: rgb(74, 47, 225);
}

/* For Tablets */
@media screen and (min-width: 481px) {
    .technologies > h1 {
        font-size: 3rem;
        margin: 0;
        width: 100%;
        padding: 1.5rem;
    }

    .technologies > p {
        font-size: 2.5rem;
        margin: 1.5rem;
        font-weight: bold;
        color: rgb(74, 47, 225);
    }
    
    .logo-container {
        grid-gap: 1rem;
        padding: 2rem .5rem 0 .5rem;
        background-color: rgba(232, 231, 248, 0.3);
        color: black;
    }

    .logo-container > img {
        width: 5rem;
    }

    .technologies {
        width: 100%;
        margin: 0 auto;
        padding-top: 6rem;
    }

    .logo-container > h1 {
        padding-bottom: 1rem;
        font-weight: 300;
        font-size: 2rem;
    }
}

/* For Laptops/Desktops */
@media screen and (min-width: 769px) {
    .logo-container {
        width: 75%;
        place-items: center;
    }
}